import {Component} from 'angular2/core';
import {MyService} from './myservice.service'

class FriendsListComponent {
    constructor (myService) {
        //this.friends = ["Alice", "Bob", "James", "Aaron"];
        this._myService = myService
    }

    static get annotations() {
        return [
            new Component({
                selector: "friends",
                template: '<ul><li *ngFor="let friend of _myService.getFriends()">{{ friend }}</li></ul>',
                providers: [MyService]
                //directives: [NgFor]
            }),
        ];
    }
}

export {FriendsListComponent};

FriendsListComponent.parameters = [[MyService]]


