import {Component} from 'angular2/core';
import {MyNameComponent} from "./myname.component";
import {FriendsListComponent} from "./friendslist.component";
import {WorldTimeComponent} from './world-time.component';

class AppComponent {

	static get annotations() {
		return [
			new Component({
	    		selector: "my-app",
	    		template: '<h1>My First Angular 2 App!!</h1><my-name></my-name><friends></friends><world-time [timeZones]="timeZones"></world-time>',
                directives: [MyNameComponent, FriendsListComponent, WorldTimeComponent]
	  		}),
		];
	}

	constructor () {
        this.timeZones = [
            'America/Los Angeles',
            'America/New York',
            'Europe/London',
            'Asia/Shanghai',
            'Australia/Sydney'
        ];
	}
}

export {AppComponent};



